import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';
import Seo from '../components/seo';

const PrivacyPolicyPage = () => {
  return (
    <Layout pageTitle="プライバシーポリシー">
      <Seo
        pageTitle="プライバシーポリシー"
        pageDescription="月の森デザインが定めるプライバシーポリシーです。個人情報の重要性と社会的責務を認識し、個人情報の管理に努めてまいります。"
      />
      <StaticImage
        alt="個人情報を、適切に管理します。"
        src="../img/page-privacy-policy.png"
        placeholder="Blurred"
      />

      <section>
        <h2>個人情報の取り扱いについて</h2>
        <p>月の森デザインは、個人情報の保護に関する法律（いわゆる個人情報保護法）、及び関連する法令等を遵守し、適切に取り扱うために、以下の通り、プライバシーポリシーを定めます。</p>
        <p>個人情報の重要性と社会的責務を認識し、個人情報の管理に努めてまいります。</p>
      </section>

      <section>
        <h2>個人情報の保護方針</h2>
        <section>
          <h3>1. 個人情報の取得</h3>
          <p>当サイトは、次の方法で個人情報を取得することがあります。</p>

          <ul>
            <li>メールフォーム（お問い合わせページ）</li>
            <li>Eメール・SMS・SNSダイレクトメッセージ</li>
            <li>その他（文書・口頭等）</li>
          </ul>
        </section>

        <section>
          <h3>2. 個人情報の利用目的</h3>
          <p>お客さまから個人情報をご提供いただく目的は、お問い合わせに対する返答や、それに伴う情報提供、その他ご連絡等、当サイトの業務を円滑に遂行するためにのみ利用させていただくものです。これらの目的以外で利用することはありません。</p>
        </section>

        <section>
          <h3>3. 個人情報の開示</h3>
          <p>次のいずれかに該当する場合を除いて、第三者へ個人情報を開示することはありません。</p>

          <ul>
            <li>お客さまご本人の同意がある場合</li>
            <li>法令に基づき、正式な開示請求があった場合</li>
            <li>人の生命や身体、財産の保護等の特殊な状況であり、且つお客さまご本人の同意を得ることが困難な場合</li>
          </ul>
        </section>

        <section>
          <h3>4. 個人情報の訂正及び利用停止</h3>
          <p>お客さまから個人情報の訂正または利用の停止を求められた場合は、ご本人さまからの請求であることを確認した上で、必要な調査を行い、妥当であると判断された場合にかぎり内容の訂正または利用停止を行います。</p>
        </section>

        <section>
          <h3>5. 個人情報の安全対策</h3>
          <p>個人情報の保有、廃棄、および漏洩・紛失・毀損・不正アクセスの防止等について、必要かつ適切な措置を講じて管理に努めます。</p>
        </section>

        <section>
          <h3>6. 個人情報の免責事項</h3>
          <p>お客さま自らが個人情報を開示した場合においては、当サイトが責任を負うことは一切ありません｡</p>
        </section>

        <section>
          <h3>7. プライバシーポリシーの改善</h3>
          <p>個人情報に関して適用される日本の法律、及び関連する法令はもとより、倫理規範に基づき、本プライバシーポリシーの内容を適宜見直し、その改善に努めます。</p>
        </section>
      </section>

      <section>
        <h2>お問い合せについて</h2>
        <p>プライバシーポリシーに関するお問い合せは、<a href="https://moon-forest-design.com/commission" title="ホームページ制作の「月の森デザイン」へのお問い合せ" target="_blank" rel="noopener noreferrer">月の森デザインのメールフォーム</a>よりご連絡ください。</p>
      </section>

      <hr />

      <p>制定： 2021年11月20日</p>

    </Layout>
  )
};

export default PrivacyPolicyPage;
